.Main{
    display: grid;

    grid-template-areas: "firstColumn secondColumn";
    grid-template-columns: 614px 293px;
    grid-gap: 50px;
    max-width: 957px;
    margin: 30px auto;
}

.box{
background: var(--background);
    border: 1px solid var(--border);
}

.box_sugestao{
    background: var(--main);

}

.container{
    display: flex;
    gap: 16px;
    padding: 15px;
}

.elementos_story {
    cursor: pointer;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(#c82d8e, #f99d4c);
}

.image_user {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 1px solid #ffff;
}

.info_story span {
    font-size: 13px;
    cursor: pointer;
}

header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px;
}

header img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.informacoes {
    display: flex;
    align-items: center;
    gap: 16px;
}

.imagem_post {
    width: 100%;
    object-fit: cover;
}

.footer_post {
    padding: 10px 15px;
}

.section_post {
    display: flex;
    justify-content: space-between;
}

.section_post .icons_post {
    display: flex;
    gap: 16px;
}

.icon {
    cursor: pointer;
}

.curtidas {
    margin-top: 6px;
}

.curtidas span {
    font-size: 15px;
    font-weight: 500;
}

.legenda {
    margin: 6px 0 3px;
}

.legenda p span {
    font-weight: 500;
}

.tempo {
    font-size: 11px;
    color: var(secondary);
}

.comentarios {
    border-top: 1px solid var(--border-secondary);
    margin-top: 8px;
    display: flex;
}

.comentarios_2 {
    padding: 15px 0;
    display: flex;
    align-items: center;
    flex: 1;
    gap: 16px;
}

.comentarios_2 input {
    font-size: 16px;
}

.comentarios button {
    color:  var(--quartary);
    cursor: pointer;
}

.sugestoes {
    display: flex;
    align-items: center;
    gap: 20px;
}

.sugestao_de_usuario {
    display: flex;
    align-items: center;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;
}

.sugestoes img {
    height: 65px;
    width: 65px;
    border-radius: 100%;
    cursor: pointer;
}

.vertudo {
    display: flex;
    justify-content: space-between;
    flex: 1;
}

.vertudo span {
    cursor: pointer;
}

.vertudo p {
    color: var(--secondary);
}

.sugestoes span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.button{
    font-weight: 500;
    color: var(--quartary);
    cursor: pointer;
}

.sugestion{
    margin-top: 5px;
}

.informacao img {
    width: 36px;
    height: 36px;
    border-radius: 100%;
    cursor: pointer;
}

.informacao {
    margin: 15px 0 0 5px;
    display: flex;
    align-items: center;
    gap: 16px;
}

.info_sugestao {
    flex: 1;
}

.informacao span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.informacao p {
    font-size: 13px;
    color: var(--secondary);
    margin-top: 3px;
}

.seguir{
    font-weight: 500;
    color: var(--quartary);
    cursor: pointer;
}

.final {
    margin-top: 25px;
    flex: 1;
}

.final p {
    font-size: 12px;
    color: var(--terciary);
    margin-bottom: 15px;
    cursor: pointer;
}
