.itens_header{
    background: var(--background);
    border-bottom: 1px solid var(--border);
}

.itens_header .logo_barra {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 60px;

    max-width: 957px;
    margin: 0 auto;
    width: 90%;
}

.itens_header .logo_barra img {
    width: 105px;
    cursor: pointer;
}

.barra_pesquisa {
    background: var(--main);
    border-radius: 3px;
    padding: 8px 20px;
    display: flex;
    align-items: center;
}

.barra_pesquisa input{
    font-size: 14px;
    padding: 0 30px;
}

.icons{
    display: flex;
    text-align: center;
    gap: 16px;
    cursor: pointer;
} 

.imagem{
    max-width: 30px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}   