:root{
    --main: #f6f6f6;
    --background: #fff;
    --primary: #262626;
    --secondary: #8e8e8e;
    --terciary: #c7c7c7;
    --quartary: #0095f6;
    --border: #dbdbdb;
    --border-segundary: #efefef;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    border: 0;
    outline: 0;
    background: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

html, body, #root{
    max-width: 100vw;
    max-height: 100vh;

    width: 100vw;
    height: 100%;
}

html{
    background: var(--main);
}

p, span {
    color: var(--primary);
}
